	module.exports = {"data":{"post":{"timeToRead":4,"canonicalURL":"https://blog.iansinnott.com/teaching-a-reactjs-workshop/","html":"<p>Who wants to be a speaker? This guy! ðŸ™Œ</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/rwr-ian-1.jpg\" alt=\"Ian Speaking at Real World React Meetup\" title=\"Ian Speaking at Real World React Meetup\"></p>\n<p>Last Sunday was a significant event for me: I put on a <a href=\"https://facebook.github.io/react/\">React</a> workshop (<a href=\"http://www.meetup.com/Real-World-React/events/228169300/\">link here</a>) to teach beginners how to get started with React. It was a full day event and it was a great learning experience for myself and hopefully for the students as well.</p>\n<h2 id=\"giving-back-to-the-community\"><a href=\"#giving-back-to-the-community\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Giving back to the community</h2>\n<p>The community surrounding React has done a lot for me. It's only been a year since I started using this technology, but it's played a tremendous role in my personal development as a programmer. As such, I really wanted to start contributing back to this amazing community that has helped nurture my own development.</p>\n<!-- more -->\n<p>I learned React and the surrounding ecosystem on my own and I know how daunting it can be. As a community we love to toss around terms like ES6, Babel, Webpack, Flux, Redux, etc. as if it's all expected knowledge for the field. And while you probably should know what all these things are after being part of the community for a while it can be very daunting when it's all new.</p>\n<p>So in creating this workshop I wanted to utilize all the knowledge I've accumulated over the past year while learning React to help speed the process for others. React has been a game changing technology for many â€“ including myself â€“ and I know it can be for others as well. They just need to know where to start.</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/rwr-ian-working-with-student.jpg\" alt=\"Ian Working with student at Real World React Meetup\"></p>\n<h2 id=\"so-much-fun\"><a href=\"#so-much-fun\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>So much fun</h2>\n<p>Something I wasn't entirely sure about going in was whether or not I would enjoy speaking. But after last Sunday I can firmly say that I <em>love</em> it. This was a revelation for me. It was hugely fun to stand at the front of the class and speak about a technology I'm passionate about, and now I want to do more of it.</p>\n<p>Moving forward I'm going to pursue more speaking opportunities, including at conferences and tech schools here in SF. The goal will continue to be to provide as much value as possible to the students and/or audience members.</p>\n<h2 id=\"room-for-improvement\"><a href=\"#room-for-improvement\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Room for improvement</h2>\n<p>The workshop went well, but it certainly wasn't perfect. There are quite a few things I would now change having done this once.</p>\n<h3 id=\"rehearse\"><a href=\"#rehearse\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Rehearse</h3>\n<p>Yup, presentations go more smoothly if you've rehearsed them beforehand. Who knew? ðŸ˜… The presentations still went quite well considering I didn't have time to rehearse, but they could have been smoother.</p>\n<h3 id=\"keep-it-concise\"><a href=\"#keep-it-concise\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Keep it concise</h3>\n<p>One of my \"lectures\" lasted a full 50 minutes. I was targeting 20 minutes and wildly overshot that, so clearly there are some wins to be had by reducing the content or splitting lectures into multiple parts. People's attentions spans are limited and as speakers we need to respect that.</p>\n<h3 id=\"no-blank-slates\"><a href=\"#no-blank-slates\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>No blank slates</h3>\n<p>I had a chance to talk to Ryan Florence recently about teaching React. If you didn't know he teaches React for a living and has a lot of good advice when it comes to teaching it. I asked him about his teaching methods and he reminded me that in a real world scenario students will almost never be facing an empty fileâ€”they will instead be editing existing code.</p>\n<p>This was something of a revelation for me. It makes perfect sense that at most companies you will be editing â€“ not creating â€“ new code, but it never crossed my mind. I work at a startup and have build all of my company's projects from scratch... but that's unusual.</p>\n<h3 id=\"provide-scrum-like-direction-during-exercises\"><a href=\"#provide-scrum-like-direction-during-exercises\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Provide Scrum-like direction during exercises</h3>\n<p>This ties into the point above about not starting from scratch. Students would have benefited from more organized exercises with action lists that they could follow to completion. Instead I left it open ended and showed students the final app before asking them to build it from scratch.</p>\n<p>Next time I will provide more structure. Since many of the students at the workshop already work at companies they would likely be used to a Scrum-like project structure with minimal user stories and such.</p>\n<h2 id=\"next-steps\"><a href=\"#next-steps\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Next Steps</h2>\n<p>My takeaways from this experience were:</p>\n<ul>\n<li>Putting on a workshop can be a ton of fun</li>\n<li>There's a serious market for learning React, especially here in SF</li>\n<li>I really enjoyed speaking and am going to pursue it further</li>\n</ul>\n<p>To that last point, I will be speaking at my first conference in a week about <a href=\"http://fitc.ca/presentation/building-static-sites-with-react/\">building static sites with React</a>.</p>\n<p>With all the current hype over React and React Native it really feels like React is eating the world. I will leave you with this image from React Conf 2016 which just wrapped up earlier this week. I think it really sums up the current sentiment in the community.</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/react-fervor.jpg\" alt=\"React Fervor\" title=\"React Fervor\"></p>","frontmatter":{"created":"2016-02-27T08:00:00.000Z","title":"Teaching a React.js Workshop","disqusId":null}}},"pathContext":{"id":"/Users/ian/dev/sites/blog.iansinnott.com/content/_posts/2016-02-27-teaching-a-reactjs-workshop.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"Going fully HTTPS (SSL) fo' free","created":"2016-04-29T07:00:00.000Z"},"slug":"going-fully-https-fo-free"},"prev":{"frontmatter":{"title":"Switching Away From Mongoose","created":"2015-08-01T07:00:00.000Z"},"slug":"switching-away-from-mongoose"}}}

